.parallax {
  background-size: cover;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hy {
  height: 100vh;
  background: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  position: relative;
  z-index: 10;
  font-family: Arial, Helvetica, sans-serif;

  color: white;
}

#aniimated-thumbnials {
  padding: 2rem 0;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#aniimated-thumbnials a {
  margin: 5px;
  flex-basis: 30%;
  flex-grow: 1;
}
#aniimated-thumbnials a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

body {
  overflow-x: hidden;
}

#map {
  height: 100%;
}

/* Optional: Makes the sample page fill the window. */
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

.ui-button {
  background-color: #fff;
  border: 0;
  border-radius: 2px;
  box-shadow: 0 1px 4px -1px rgba(0, 0, 0, 0.3);
  margin: 10px;
  padding: 0 0.5em;
  font: 400 18px Roboto, Arial, sans-serif;
  overflow: hidden;
  height: 40px;
  cursor: pointer;
}

.ui-button:hover {
  background: #ebebeb;
}
